#skills{
    overflow: hidden;
    width: 100vw;
    max-width: 65rem;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin: 0 auto;
    padding-top: 3rem;
}

.skillTitle{
    font-size: 3rem;
    font-weight: 600;
    padding-top: 3rem;
    margin-bottom: 1.5rem;
}
.skillDiscription{
    font-weight: 200;
    font-size: 1rem;
    max-width: 51rem;
    padding: 0 2rem;

}
.skillBars{
    margin: 1.5rem;
    width: 100vw;
    max-width: 80%;
    text-align: left;
}
.skillBar{
    display: flex;
    margin: 1rem;
    padding: 1.5rem 2rem;
    border-radius: 0.5rem;
    background: rgb(50, 50, 50);

}
.skillBarImg{
    object-fit: cover;
    height: 3rem;
    width: 3rem;
    margin-right: 2rem;


}
.skillBarText>p{
    font-size: 1rem;
    font-weight: 200;
}
.skillBarText>h2{
    font-size: 5vw;
    color: yellow;

}

.btn{
    background: white;
    margin: 1rem 0;
    padding: 0.75rem 2rem;
    border: none;
    border-radius: 2rem;

}
.btnImg{
    object-fit: cover;
    margin: 0 0.25rem;
    height: 1rem;

}
@media screen and(max-width:720px){
    .skillBarText>p{
        font-size: 2vw;
    }
}
@media screen and(max-width:450px){
    .skillBarText>p{
        font-size: 2vw;
    }
    .skillBarImg{
        height: 2.25rem;
        width: 3vw;
    }
    .skillBarText>h2{
        font-size: 5vw;
        color: yellow;

    }
}